// -------------- THEME SWITCHER -------------- //
@mixin theme($--bg-color, $--primary-text-color, $--secondary-text-color, $--link-color, $--visited-link-color, $--highlight) {
  background-color: $--bg-color;

  color: $--primary-text-color;

  a {
    color: $--link-color;
    &:visited { color: $--visited-link-color; }
  }

  details {
    border: thin solid $--primary-text-color;
  }

  details summary {
    color: $--primary-text-color;
  }

  details[open] summary {
    border-bottom: 1px solid $--primary-text-color;
  }

  pre {
    background: $--bg-color;
  }

  code:not(pre > code) {
    background-color: $--primary-text-color;
    color: $--bg-color;
  }

  *:target {
    background: $--highlight;
    color: $--primary-text-color;
   }

  table, th, td {
    border: thin solid $--primary-text-color;
  }

  .toc {
    border: thin solid $--primary-text-color;
    padding: 1rem;
  }

  figcaption { color: $--secondary-text-color; }

  blockquote {
    border: thin solid $--primary-text-color;
  }
}

@mixin dark-appearance {
  @include theme(#212121, #fafafa, #000000, #fafafa, #fafafa, #FFFF00);
}

@mixin light-appearance {
  @include theme(#fffdfa, #212121, #666666, #212121, #212121, #FFFF00);
}

body[themetoggle="dark"] { @include dark-appearance; }
body[themetoggle="light"] { @include light-appearance; }

@media (prefers-color-scheme: dark) {
  body[themetoggle="auto"] { @include dark-appearance; }
}

@media (prefers-color-scheme: light) {
  body[themetoggle="auto"] { @include light-appearance; }
}

// -------------------------------------------- //

@import url('https://chinese-fonts-cdn.deno.dev/packages/jhlst/dist/%E4%BA%AC%E8%8F%AF%E8%80%81%E5%AE%8B%E4%BD%93v2_002/result.css'); 

body {
  font-family: 'KingHwa_OldSong'; // 字体
  font-size: 16px; // 字号
  line-height: 1.4; // 行高
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word; // 自动换行
}

h2, h3, h4, h5, h6 { margin-top: 1.5rem; }

h1 {
    margin: 0.83em 0 0 0; 
}

p { margin: 1rem 0; }

li { margin: 0.4rem 0; }

a {
  text-decoration: underline; 
  &:hover { text-decoration: underline; } 
  text-decoration-color: red;
  text-underline-offset: 4px;
}

hr {
  text-align: center;
  border: 0;
  margin: 1rem 0;

  &:before { content: '/////' }
  &:after { content: attr(data-content) '/////' }
}

pre {
  padding: 1em;
  overflow-x: auto; /* Fix pre content overflowing parent without scroll bar */
}

table { width: 100%; }

table, th, td {
  border-collapse: collapse;
  padding: 0.4rem;
}

code {
  text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

code:not(pre > code) {
  padding: 0.1em 0.2em;
  font-size: 90%;
}

code.has-jax {
  -webkit-font-smoothing: antialiased;
  background: inherit !important;
  border: none !important;
  font-size: 100%;
}

blockquote {
  padding: 1rem;
  font-style: italic;

  p { margin: 0; }
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

figcaption {
  text-align: center;
  opacity: 0.5;
}

details {
  padding: 1rem;
}

details summary {
  text-decoration: none;
}

details[open] summary {
  margin-bottom: 0.5em;
  padding-bottom: 0.5em;
}

.post-meta {
  text-align: left;
  font-size: 13px;
  line-height: 0.2;
}

.head-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  max-width: 768px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.toc {
  padding: 1rem;
}

.footer {
  text-align: center;
  font-size: 12px;
  line-height: 1;
  margin-bottom: 20px;
}